export default {
	data() {
		return {
			message: '',
			reminNum: 200,
			// 图片存放地址
			imagePathList: [],
			// 每个图片的路径（解决了数据监控不到的问题）
			// 提交的数据
			imagePath: [],
			realPathList:[],
			// 控制盒子数量
			imgList: ['']
		}
	},

	methods: {

		chooseImg(i) {
			uni.chooseImage({
				count: 3,
				sizeType: "original",
				success: (res) => {
					this.imagePathList[i] = res.tempFilePaths;
										
					this.$set(this.imagePath, i, res.tempFilePaths[0])
										
					this.imgList.length !== 3 && this.imgList.push('');
					console.log(this.imagePath)
					
					//因为有一张图片， 输出下标[0]， 直接输出地址
					var imgFiles = res.tempFilePaths[0]
					// 上传图片
					// 做成一个上传对象
					let token = uni.getStorageSync('USER_TOKEN');
					let that = this;
					var uper = uni.uploadFile({
						// 需要上传的地址
						url: 'http://192.168.0.104:8001/upload/picture',
						header: {
							'Token': token,
							'Content-Type': 'multipart/form-data',
							'Accept': 'application/json'
						},
						// filePath  需要上传的文件
						filePath: imgFiles,
						name: 'file',
						success(res1) {
							// 显示上传信息
							const realPath = JSON.parse(res1.data).result
							that.realPathList[i] = realPath
							console.log(res1)
						}
					});

				}
			})
		this.$emit("showPath", this.realPathList)
		}
		
	}
}
